    font-family: Roboto, 'Courier New', Courier, monospace;
    color: white;
}

/*Animations*/
@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.fade-in {
    animation: fadeIn 0.1s forwards ease-in-out;
}

.fade-out {
    opacity: 0;
    transition: all 0.1s ease;
}

/*main style*/

html,
body {
    display: border-box;
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #020617;
}

#main-content {
    height: 600px;
    width: 900px;
    display: grid;
    grid-template: 1fr 6fr/2fr 5fr;
    background-color: #1e1b4b;
    border-radius: 1em;
    overflow-y: auto;
}

.todo-header {
    grid-area: 1/2/2/-1;
    background-color: #312e81;
    border-radius: 0 1em 0 0;

    display: flex;
    justify-content: flex-start;
    gap: 1em;
    align-items: center;

    .header-title {
        font-weight: 700;
        font-size: large;
        margin-left: 2em;
    }
}

.project-container,
.todo-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 1em;
    padding: 2em;
}

.project-header {
    grid-area: 1/1/2/2;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1em;
    padding: 2em;
    background-color: #0c4a6e;
    border-radius: 1em 0 0 0;
}

.project-container {
    grid-area: 2/1/-1/2;
    background-color: #3730a3;
    border-radius: 0 0 0 1em;
}

.todo-container {
    grid-area: 2/2/-1/-1;
}

.project-container .project-item,
.todo-container .todo-item {
    padding: 1em;
    border-radius: 1em;
    cursor: pointer;
}

.project-container .project-item:hover,
.todo-container .todo-item:hover {
    transition: all 0.2s ease;
    transform: scale(1.01);
}

.project-container .project-item {
    display: flex;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    justify-content: space-between;
    align-items: center;

    background-color: #1e1b4b;

    .project-title {
        margin-left: auto;
    }
}

.todo-container .todo-item {
    display: flex;
    z-index: 2;
    justify-content: space-between;
    align-items: center;

    background-color: #5b21b6;

    .todo-title {
        margin-left: 1em;
        margin-right: auto;
        white-space: nowrap;
        overflow: hidden;
        overflow-wrap: break-word;
        text-overflow: ellipsis;
        max-width: 250px;
    }
}

.project-delete-icon,
.todo-delete-icon,
.todo-view-icon,
.todo-edit-icon,
.project-edit-icon {
    content: '';
    display: block;
    height: 1.5em;
    width: 1.5em;
    background-position: initial;
    background-size: initial;
    background-repeat: initial;
    fill: white;
}

.project-delete-icon:hover,
.todo-delete-icon:hover,
.todo-view-icon:hover,
.todo-edit-icon:hover,
.project-edit-icon:hover {
    transition: all 0.2s ease;
    transform: scale(0.90);
} 

