.todo-edit-icon {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.form-dialog,
.form-view-dialog {
    border-radius: 1em;
    position: fixed;
    z-index: 10;
    padding: 2em;
    transform: translateY(-10%);
    border-color: transparent;
    border-radius: 2em;
    background-color: #1e3a8a;
}

.form-view-dialog {
    padding: 0;
    border-radius: 1em;
    background-color: #111827;
}

.toast-dialog {
    border-radius: 1em;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    padding: 1em;
    transform: translateY(10%);
    border-color: transparent;
    border-radius: 2em;
    background-color: #991b1b;
}

.dialog-background {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9;
    background-color: black;
    opacity: 0.2;
}

.project-form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1em;
    align-items: center;

    .project-form-content {
        display: grid;
        grid-template: auto/1fr 1fr;
        column-gap: 1em;
        justify-items: center;
        align-items: center;
        padding: 1em;
    }

    .form-buttons {
        display: grid;
        grid-template: auto/1fr 1fr;
        column-gap: 1em;
    }
}

.todo-form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1em;
    align-items: center;

    .todo-dialog-content {
        display: grid;
        grid-template: auto/1fr 1fr;
        column-gap: 1em;
        justify-items: center;

        .main-info-field,
        .additional-info-field {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 1em;
        }
    }

    .form-buttons {
        display: grid;
        grid-template: auto/1fr 1fr;
        column-gap: 1em;
    }
}

.todo-view-container {
    display: grid;
    grid-template: auto 5fr/auto;
    gap: 1em;
    align-items: center;

    .todo-view-header {
        display: flex;
        gap: 1em;
        align-items: center;
        justify-content: space-between;
        padding: 2em;
        border-radius: 1em;

        .todo-header-title {
            font-size: x-large;
            font-weight: 700;
            margin-right: auto;
        }

        .todo-header-date {
            margin-left: auto;
        }

        .todo-header-date,
        .todo-header-priority {
            font-size: small;
            color: #9ca3af;
            font-weight: 200;
        }

        .todo-header-title,
        .todo-header-date,
        .todo-header-priority {
            white-space: nowrap;
            overflow: hidden;
            overflow-wrap: break-word;
            text-overflow: ellipsis;
            max-width: 250px;
        }
    }

    .todo-view-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1em;
        background-color: #0f172a;
        padding: 1em;
        border-radius: 1em;
    }

    .secondary-button {
        margin: 1em;
    }
}

.primary-button,
.secondary-button {
    padding: 0.5em 1em;
    border-color: transparent;
    border-radius: 1em;
}

.sort-priority-button,
.new-project-button,
.new-todo-button {
    padding: 0.5em 1em;
    border-color: transparent;
    border-radius: 1em;
    background-color: #083344;
}

.primary-button:hover,
.secondary-button:hover,
.new-project-button:hover,
.new-todo-button:hover,
.sort-priority-button:hover {
    transition: all 0.2s ease;
}

.new-project-button:hover,
.new-todo-button:hover,
.sort-priority-button:hover {
    background-color: #164e63;
}

.primary-button {
    background-color: #15803d;
}

.primary-button:hover {
    background-color: #16a34a;
}

.secondary-button {
    background-color: #b91c1c;
}

.secondary-button:hover {
    background-color: #dc2626;
}

textarea,
input[type="text"],
input[type="date"] {
    appearance: none;
    border-color: transparent;
    border-radius: 1em;
    background-color: #1e1b4b;
    padding: 0.5em;
}

textarea {
    resize: none;
}

/*Checkbox*/
input[type="checkbox"] {
    /* Add if not using autoprefixer */
    -webkit-appearance: none;
    /* Remove most all native input styles */
    appearance: none;
    /* For iOS < 15 */
    background-color: white;
    /* Not removed via appearance */
    margin: 0;

    font: inherit;
    color: currentColor;
    width: 1.15em;
    height: 1.15em;
    border: 0.15em solid currentColor;
    border-radius: 0.15em;
    transform: translateY(-0.075em);

    display: grid;
    place-content: center;

    &::before {
        content: "";
        width: 0.65em;
        height: 0.65em;
        clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
        transform: scale(0);
        transform-origin: bottom left;
        transition: 120ms transform ease-in-out;
        box-shadow: inset 1em 1em var(rebeccapurple);
        /* Windows High Contrast Mode */
        background-color: CanvasText;
    }

    &:checked::before {
        transform: scale(1);
    }
